import "./App.scss";
import {
  Box,
  Button,
  DataTable,
  DateInput,
  Form,
  FormField,
  grommet,
  Grommet,
  Heading,
  RangeInput,
  Select,
  Text,
  TextInput,
} from "grommet";

import store from "./store";
import { observer } from "mobx-react";

function App() {
  const {
    options,
    columns,
    select,
    selectItem,
    birthday,
    dateStart,
    dateStop,
    open,
    selectData,
    rangeInput,
    insurancePolicyCost,
    disabled,
    value,
  } = store;

  return (
    <Grommet theme={grommet}>
      <Box align="center" pad="small">
        <Heading level="3" textAlign="center">
              
        </Heading>
      </Box>
      <Box fill align="center" justify="center" pad="xxsmall" direction="row">
        <Form
          value={value}
          onChange={(nextValue) => store.setValue(nextValue)}
          onReset={() => store.handleReset()}
          onSubmit={({ value }) => store.handleSubmit(value)}
        >
          <FormField name="growth" htmlFor="growth" label="" required>
            <TextInput name="growth" placeholder="   ." />
          </FormField>
          <FormField name="weight" htmlFor="weight" label="" required>
            <TextInput name="weight" placeholder="   ." />
          </FormField>
          <FormField name="sex" htmlFor="sex" label="" required>
            <Select
              name="sex"
              placeholder=" "
              value={select}
              options={options}
              onChange={({ option }) => store.setSelect(option)}
            />
          </FormField>

          <FormField
            name="birthday"
            label=" "
            htmlFor="birthday"
            required
          >
            <DateInput
              name="birthday"
              format="dd.mm.yyyy"
              onChange={({ value }) => store.setBirthDay(value)}
              value={birthday}
              calendarProps={{ size: "small" }}
            />
          </FormField>

          <Box direction="row" gap="medium" justify="center">
            <Button type="submit" primary label="" />
            <Button type="reset" label="" />
          </Box>
        </Form>
      </Box>

      {open && (
        <Box align="center" pad="medium">
          <Box align="center" pad="medium">
            <Box margin="medium" width="large">
              <DataTable
                columns={columns}
                data={selectData}
                step={10}
                select={selectItem}
                onSelect={(value) => store.setSelectItem(value)}
                background={["white", "light-2"]}
                sortable
              />
            </Box>
            {selectItem.length > 0 && (
              <Box
                margin="medium"
                className="risks"
                pad="small"
                round="small"
                width="large"
              >
                <Box alignSelf="start" className="risks__title">
                  :
                </Box>
                {selectItem.map((item) =>
                  selectData.map((datum) =>
                    item === datum.name ? (
                      <Box key={datum.id} alignSelf="start">
                        {datum.risks}
                      </Box>
                    ) : null,
                  ),
                )}
              </Box>
            )}
          </Box>
          <Box align="center" pad="small" width="large">
            <RangeInput
              value={rangeInput}
              onChange={(value) => store.handleChangeRangeInput(value)}
              step={1}
              min={12}
              max={24}
              disabled={disabled}
            />
          </Box>
          <Box align="center" pad="xsmall">
            <Text>  {rangeInput} </Text>
          </Box>
          <Box
            fill
            align="center"
            justify="center"
            pad="large"
            direction="row"
            gap="large"
          >
            <Box width="medium" gap="" direction="row">
              <Text tag="div"> </Text>
              <DateInput
                format="dd.mm.yyyy"
                value={dateStart}
                onChange={({ value }) =>
                  store.handleCalculation(value, rangeInput)
                }
                calendarProps={{ size: "small" }}
              />
            </Box>
            <Box width="medium" gap="medium" direction="row">
              <Text tag="div"> </Text>
              <DateInput
                format="dd.mm.yyyy"
                value={dateStop}
                onChange={() => { }}
                calendarProps={{ size: "small" }}
                disabled
              />
            </Box>
          </Box>
          <Box
            width="large"
            gap="large"
            direction="row"
            align="center"
            justify="center"
          >
            <Text tag="div">
                : {insurancePolicyCost}
            </Text>
            <Button
              type="button"
              primary
              label=" "
              onClick={() => store.messageInsurancePolicy()}
              disabled={disabled}
            />
          </Box>
        </Box>
      )}
    </Grommet>
  );
}

export default observer(App);
